<template>
    <div class="show-message">
        <el-table :data="tableData" style="width: 100%;" border v-if="isShow">
            <el-table-column prop="type" label="类型" width="50">
                <template slot-scope="scope">
                    <span>{{scope.row.type == 1 ? '文本消息' : '图文消息'}}</span>
                </template>  
            </el-table-column>
            <el-table-column prop="task" label="是否延时" width="50">
                <template slot-scope="scope">  
                    {{scope.row.task == true ? '是' : '否'}}  
                </template>
            </el-table-column>
            <el-table-column prop="delay" label="延时时间" width="50">
                <template slot-scope="scope">  
                    {{scope.row.task == true ? scope.row.delay : '——'}}  
                </template>
            </el-table-column>
            <el-table-column prop="contents[0].title" label="标题" width="100"></el-table-column>
            <el-table-column prop="contents[0].des" label="详细信息" width="300"></el-table-column>
            <el-table-column prop="contents[0].url" label="链接" width="150"></el-table-column>
            <el-table-column prop="contents[0].img" label="图片" width="200">
                <!-- <template slot-scope="scope">
                    <img v-if="scope.row.imgUrl" :src="scope.row.imgUrl" width="100%">
                    <el-button v-else-if="scope.row.type == 2" size="small" type="success" @click="showPic(scope.$index, scope.row)">显示图片</el-button>
                </template> -->
            </el-table-column>
            <el-table-column label="操作">
                 <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="editMessage(scope.$index, scope.row)">修改</el-button>
                    <el-button v-show="!scope.row.task" size="small" type="success" @click="sendMessage(scope.$index, scope.row)">发送消息</el-button>
                    <el-button size="small" type="danger" @click="deleteMessage(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <edit-message v-else></edit-message>
    </div>
</template>

<script>
    import Axios from 'axios'
    import store from '../../vuex/store'
    import editMessage from '../editMessage/editMessage'
    export default {
        computed:{
            isShow() {
                return store.state.isShow;
            }
        },
        components: {
            editMessage
        },
        data() {
            return {
                tableData: [],
                // imgUrl: ''
                desShow: ''
            }
        },
        mounted() {
            this.showMessage()
            // console.log(this.$refs.des)
            // this.desShow = this.$refs.des.prop
        },
        methods: {
            // showPic(index, row) {
            //     console.log(row)
            //     row.imgUrl = row.contents[0].img
            // },
            showMessage() {
                Axios({
                    url: '/message_1',
                    method: 'get',
                    parmas: {},
                    withCredentials: true
                }).then((res) => {
                    // console.log(res.data.messages)
                    if(res.data.messages.length != 0) {
                        this.tableData =res.data.messages
                    }
                })
            },
            editMessage(index, row) {
                store.commit('setMessageData', row)
                store.commit('changeisShow', false)
            },
            sendMessage(index, row) {
                this.$confirm('此操作发送此条客服消息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/message_1/send',
                        method: 'get',
                        params: {
                            id: row._id
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: res.data.success
                        })
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消发送'
                    });          
                });
            },
            deleteMessage(index, row) {
                this.$confirm('此操作删除此条客服消息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/message_1/delete',
                        method: 'get',
                        params: {
                            id: row._id
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: res.data.success
                        })
                        this.showMessage()
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
                
            }
        }
    }
</script>

<style scoped>
    td.el-table_1_column_3 {
        max-height: 100px;
    }
</style>




