<template>
    <div class="add-message">
        <el-form ref="form" :model="form" label-width="80px" size="mini">
            <el-form-item>
                 <h2>修改客服消息</h2>
            </el-form-item>
            <el-form-item label="类型">
                <el-radio-group v-model="type" size="medium" @change="change">
                    <el-radio border label="1">文本消息</el-radio>
                    <el-radio border label="0">图文消息</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form ref="subForm" :model="subForm" label-width="80px" size="mini" v-if="type == 1 ? false : true">
                <el-form-item label="图片">
                    <el-upload :show-file-list="false" enctype="multipart/form-data" name="imageFile" action="http://crm.rrdtjj.top/message_1/upload" :on-success="handleAvatarSuccess">
                        <el-button size="small" type="primary">点击上传</el-button>
                        <img v-if="imgUrl" :src="imgUrl" alt="" width="200">
                    </el-upload>
                </el-form-item>
                <el-form-item label="标题">
                    <el-input v-model="subForm.title" style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item label="链接">
                    <el-input v-model="subForm.url" style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item label="详细信息" prop="des">
                    <el-input type="textarea" v-model="subForm.des" style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item size="large">
                    <el-button type="success" @click="addOne">添加</el-button>
                    <el-button type="primary" @click="saveOne">保存</el-button>
                </el-form-item>
                <el-form-item label="图文消息" prop="pic_message">
                    <el-table :data="picMessageData" style="width: 100%;" border>
                        <el-table-column prop="title" label="标题" width="100"></el-table-column>
                        <el-table-column prop="img" label="图片" width="200">
                            <template slot-scope="scope">
                                <img :src="scope.row.img" width="100%">
                            </template>
                        </el-table-column>
                        <el-table-column prop="url" label="链接" width="200"></el-table-column>
                        <el-table-column prop="des" label="详细信息" width="300"></el-table-column>
                        <el-table-column label="操作">
                            <template slot-scope="scope">                          
                                <el-button size="small" type="primary" @click="updateOne(scope.$index, scope.row)">修改</el-button>
                                <el-button size="small" type="success" class="send-message" @click="deleteOne(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table> 
                </el-form-item>
            </el-form>
            <el-form-item v-else label="详细信息" prop="des">
                <el-input type="textarea" v-model="des" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="是否延时">
                <el-switch v-model="task"></el-switch>
            </el-form-item>
            <el-form-item label="延时时间">
                <el-input v-model="delay" style="width:100px;"></el-input>
            </el-form-item>
            <el-form-item label="公号" prop="codes">
                <el-checkbox-group v-model="codes">
                    <el-checkbox v-for="(item, index) in gonghaoList" :key="index" :label="item.code" name="codes">{{item.name}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item size="large">
                <el-button type="primary" @click="onSave">保存</el-button>
                <el-button @click="cancel">取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import Axios from 'axios'
    import store from '../../vuex/store'
    export default {
        data() {
            return {
                form: {},
                type: '1',
                subForm: {},
                picMessageData: [],
                codes: [],
                contents: [],
                task: false,
                delay: 1,
                des: '',
                picMessageSwitch: false,
                imgUrl: '',
                gonghaoList: [],
                obj: {},
                contentData: {}
            };
        },
        mounted() {
            this.get_codes()
            this.showList()
        },
        methods: {
            onSave() {
                if (this.type == 1) {
                    this.contentData.des = this.des
                    this.contents.push(this.contentData) 
                } else {
                    this.contents = this.picMessageData
                }
                Axios({
                    url: '/message_1/update',
                    method: 'post',
                    data: {
                        id: this.obj._id,
                        contents: this.contents,
                        type: this.type,
                        task: this.task,
                        delay: this.delay,
                        codes: this.codes
                    }
                }).then((res) => {
                    if(res.data.success) {
                        this.$message({
                            type: 'success',
                            message: res.data.success
                        })
                        window.location.href = '/'
                    } else {
                        this.$message.error(res.data.err)
                    }
                })
            },
            addOne() {
                this.subForm.img = this.imgUrl
                this.picMessageData.push(this.subForm)
                this.subForm = {}
                this.imgUrl =''
            },
            saveOne() {
                this.picMessageData.splice(this.subForm.index, 1, this.subForm)
                this.subForm = {}
                this.imgUrl =''
            },
            change() {
                this.form = {}
            },
            handleAvatarSuccess(res, file) {
                this.imgUrl = 'http://crm.rrdtjj.top/uploads/' + res.filename
            },
            updateOne(index, row) {
                this.subForm = row
                this.subForm.index = index
                this.imgUrl= this.subForm.img
            },
            deleteOne(index, row) {
                this.$confirm('此操作删除此条客服消息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.picMessageData.splice(index, 1)
                    this.showList()
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
            },
            showList() {
                this.obj = store.state.messageData
                this.picMessageData = this.obj.contents
                this.type = this.obj.type.toString()
                this.codes = this.obj.codes
                this.des = this.obj.contents[0].des
            },
            cancel() {
                // store.commit('changeisShow', true)
                window.location.href = '/'
            },
            get_codes() {
                Axios({
                    url: '/message_1/get_code',
                    method: 'get',
                    params: {}
                }).then(res => {
                    if(res.data.codes != '') {
                        this.gonghaoList = res.data.codes
                    }
                })
            }
        }
    };
</script>

<style scoped>
    .add-message {
        border: 1px solid #eee;
        padding: 20px;
    }
</style>




