<template>
    <div class="home">
        <el-menu :default-active="onRoutes" class="el-menu-demo" mode="horizontal" unique-opened router>
            <template v-for="item in navlist">
				<el-menu-item :index="item.index" :key="item.index"> 
					<span slot="title">{{item.title}}</span>
				</el-menu-item>
			</template>
        </el-menu>
        <div class="field">
            <transition name="move" mode="out-in">
                <keep-alive>
                    <router-view></router-view>
                </keep-alive>
            </transition>
        </div>
    </div>
</template>

<script>
    export default {
        computed:{
            onRoutes(){
                return this.$route.path.replace('/','');
            }
        },
        data() {
            return {
                activeIndex: '1',
                activeIndex2: '1',
                navlist: [{
					index: "showMessage",
					title: "首页"
				}, {
					index: "addMessage",
					title: "添加客服消息"
				}]
            };
        },
        methods: {
            
        }
    }
</script>

<style scoped>
    .home {
        margin-top: 30px;
    }

    .field {
        margin-top: 30px;
    }

</style>



