import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
var state={
	messageData: {},
	isShow: true
};
var mutations={
	changeisShow: function(state, data) {
        state.isShow = data;
	},
	setMessageData(state, data) {
		if (data) {
			state.messageData = data
        } else {
            state.messageData = {}
        }
	}
};
export default new Vuex.Store({
	state,
	mutations
})



